// Custom Theming for NG-ZORRO
// For more information: https://ng.ant.design/docs/customize-theme/en
@import "../node_modules/ng-zorro-antd/ng-zorro-antd.less";

// Override less variables to here
// View all variables: https://github.com/NG-ZORRO/ng-zorro-antd/blob/master/components/style/themes/default.less

// @primary-color: #1890ff;
@import "~@angular/cdk/overlay-prebuilt.css";
/* You can add global styles to this file, and also import other style files */
@font-face {
  font-family: "SourceSerifPro";
  src: url(^assets/fonts/SourceSerifPro-Regular.otf);
}
@font-face {
  font-family: "SourceSansPro";
  src: url(^assets/fonts/SourceSansPro-Regular.otf);
}

@font-face {
  font-family: "NFIcons";
  src: url(^assets/fonts/NFIcons-Regular.woff2);
}

body {
  height: 100vh;
}
p {
  margin-top: 0;
}
textarea {
  -webkit-font-smoothing: antialiased;
  resize: none;
  outline-style: none;
}

.insertion {
  animation-name: insertion-blink;
  animation-timing-function: unset;
  animation-duration: 0.9s;
  animation-direction: reverse;
  animation-iteration-count: infinite;
}

@keyframes insertion-blink {
  from {
    opacity: 0;
  }
  to {
    opacity: 0.9;
  }
}

unselectable {
  user-select: none;
}

.measure-handle {
  cursor: pointer;
  z-index: 999;
}

.note-action-overlay {
  pointer-events: none;
}

.nf-icon {
  word-wrap: normal;
  -webkit-font-smoothing: antialiased;
  text-rendering: optimizeLegibility;
  direction: ltr;
  font-family: NFIcons;
  font-style: normal;
  font-weight: 400;
  letter-spacing: normal;
  line-height: 1;
  text-transform: none;
  white-space: nowrap;
  z-index: 1;
}

.nf-icon.tempoDot::before {
  content: "\e129";
  vertical-align: bottom;
}
.nf-icon.note2::before {
  content: "\e312";
}
.nf-icon.note3::before {
  content: "\e121";
}
.nf-icon.note4::before {
  content: "\e120";
}
.nf-icon.note5::before {
  content: "\e119";
}
.nf-icon.note6::before {
  content: "\e118";
}
.nf-icon.note7::before {
  content: "\e117";
}
.nf-icon.note8::before {
  content: "\e116";
}
